import {
    Component,
    h,
    Host,
    Prop,
    Event,
    EventEmitter,
    Element,
} from '@stencil/core';

export type InputPosition = 'before' | 'after';

/**
 * @category Form Controls
 * @description A range input component for selecting a value within a specified range.
 */

@Component({
    tag: 'atui-input-range',
    styleUrl: 'atui-input-range.scss',
    shadow: false,
})
export class AtuiInputRangeComponent {
    /**
     * Label of the input.
     */
    @Prop()
    label?: string;

    /**
     * Short description or validation hint if required.
     */
    @Prop()
    hint_text?: string;

    /**
     * Info icon with detailed tooltip description if required. Content is available via info icon tooltip.
     */
    @Prop()
    info_text?: string;

    /**
     * suffix message for the input.
     */
    @Prop()
    suffix?: string;

    /**
     * Warning message for the input.
     */
    @Prop()
    warning_text?: string;

    /**
     *
     * Error message visible when input is invalid.
     */
    @Prop()
    error_text?: string;

    /**
     * Set the input to an invalid state.
     */
    @Prop()
    invalid?: boolean;

    /**
     * Set the input to a readonly state.
     */
    @Prop()
    readonly?: boolean;

    /**
     * Disable user interaction. Disabled state should be applied via form control.
     */
    @Prop()
    disabled?: boolean;

    /**
     * Indicated form field is required.
     */
    @Prop()
    required?: boolean;

    /**
     * Numeric value of the slider component.
     */
    @Prop({ mutable: true })
    get value(): number {
        return this._value;
    }
    set value(val: number) {
        this._value = Math.max(this.min, Math.min(this.max, val));
    }
    private _value: number;
    /**
     * Min selectable value of the slider.
     */
    @Prop()
    min = 0;

    /**
     * Max selectable value of the slider.
     */
    @Prop()
    max = 100;

    /**
     * Interval between selections.
     */
    @Prop()
    step?: number = 1;

    /**
     * Display min and max text values.
     */
    @Prop()
    show_minmax?: boolean = true;

    /**
     * Display step indicators.
     */
    @Prop()
    show_ticks?: boolean = true;

    /**
     * Display order of input and slider.
     */
    @Prop()
    show_value?: boolean = true;

    /**
     * Display numeric input with slider.
     */
    @Prop()
    show_input?: boolean = true;

    /**
     * Position of the numeric input, left or right of the slider.
     */
    @Prop()
    label_position: InputPosition = 'after';

    /**
     * Emits an event containing the current value when the text input or slider input's content is changed
     */
    @Event()
    atuiChange: EventEmitter<number>;

    @Element() el: HTMLElement;
    private sliderContainerRef: HTMLDivElement;
    private sliderId = `slider-${Math.random().toString(36).substring(2, 11)}`;

    onChange(inputVal: number): void {
        const newValue = Math.max(this.min, Math.min(this.max, inputVal)); // Clamp value
        this.value = newValue;
        this.atuiChange.emit(this.value);
    }

    render() {
        return (
            <Host>
                <div class="flex flex-col">
                    <slot name="label" />
                    {(this.label || this.required || this.info_text) && (
                        <atui-form-label
                            label={this.label}
                            for={this.sliderId}
                            required={this.required && !this.readonly}
                            info_text={this.info_text}
                        />
                    )}
                    {this.hint_text && (
                        <span
                            class="inline-block text-xs leading-tight text-light"
                            data-name="slider-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                    {this.error_text && this.invalid && (
                        <span
                            class="text-xs font-normal text-error"
                            data-name="slider-error"
                        >
                            {this.error_text}
                        </span>
                    )}
                    {this.warning_text && !this.invalid && (
                        <span
                            class="text-xs font-normal text-warning-foreground"
                            data-name="slider-warning"
                        >
                            {this.warning_text}
                        </span>
                    )}
                </div>

                <div
                    class={`mt-4 flex items-start gap-8 ${this.label_position == 'before' ? 'flex-row-reverse' : 'flex-row'}`}
                >
                    <div class="flex flex-grow flex-col">
                        <div class="flex flex-grow">
                            <div
                                class="range-slider"
                                style={{
                                    '--min': `${this.min}`,
                                    '--max': `${this.max}`,
                                    '--step': `${this.step}`,
                                    '--value': `${this.value}`,
                                    '--text-value': `${this.value}`,
                                    '--show-min-max': `${this.show_minmax ? 'block' : 'none'}`,
                                }}
                                ref={(el) => (this.sliderContainerRef = el)}
                            >
                                <input
                                    list="markers"
                                    type="range"
                                    min={this.min}
                                    max={this.max}
                                    step={this.step}
                                    aria-controls={this.sliderId}
                                    disabled={
                                        this.disabled || this.readonly
                                            ? true
                                            : undefined
                                    }
                                    id={this.sliderId}
                                    value={this.value}
                                    class="custom-slider"
                                    data-name="slider-range-input"
                                    onInput={(event) => {
                                        const newValue = parseInt(
                                            (event.target as HTMLInputElement)
                                                .value,
                                        );
                                        this.value = newValue;
                                        this.onChange(newValue);
                                        this.sliderContainerRef.style.setProperty(
                                            '--value',
                                            String(this.value),
                                        );
                                        this.sliderContainerRef.style.setProperty(
                                            '--text-value',
                                            JSON.stringify(this.value),
                                        );
                                    }}
                                />
                                {this.show_value && (
                                    <output class="text-sm">
                                        {this.value}
                                    </output>
                                )}
                                <div class="range-slider__progress"></div>
                            </div>
                        </div>
                    </div>
                    {this.show_input && (
                        <div class="relative flex w-80 flex-row items-center gap-8">
                            <atui-input-numeric
                                readonly={this.readonly}
                                disabled={this.disabled}
                                min={this.min}
                                max={this.max}
                                step={this.step}
                                value={this.value}
                                show_buttons={false}
                                data-name="slider-text-input"
                                onAtuiChange={(event) =>
                                    this.onChange(event.detail as number)
                                }
                            />
                            {this.suffix && (
                                <span
                                    data-name="slider-suffix"
                                    class="absolute right-[36px] top-1/2 -translate-y-1/2 text-light"
                                >
                                    {this.suffix}
                                </span>
                            )}
                        </div>
                    )}
                </div>
            </Host>
        );
    }
}
